.text-center-align {
  text-align: center;
}

.text-4em {
  font-size: 4em;
}

.text-3em {
  font-size: 3em;
}

.text-2em {
  font-size: 2em;
}

.text-1em {
  font-size: 1em;
}

.post-cover {
  width: 40em;
  box-shadow: rgba(0, 0, 0, 0.24) 0px -3px 9px;
}

.post-container {
  margin: 0 auto;
  width: 40em;
}

@media screen and (max-width: 800px) {
  .post-cover {
    // display: none;
  }

  .post-container {
    width: 100%;
  }

  .post-details {
    padding: 10px !important;
  }
}

.post-link, .post-details, .post-excerpt, .post-link-container {
  margin-bottom: 0;
}

.post-link {
  float: left;
  clear: both;
  width: 70%;
}

.post-meta {
  float: right;
}

.post-excerpt {
  margin: 15px 0px;
  display: inline-block;
}

.the-post {
  margin-bottom: 4em !important;
}

.post-details {
  padding: 10px 10px 5px 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

/* Overrides */
.post-title {
  font-size: 3em !important;
}

article.detailed {
  padding-top: 75px;
  margin-top: -35px;
}

main.page-content {
  padding: 0px !important;
}

.footer-heading {
  visibility: hidden;
}

.post-list-heading {
  font-size: 3em !important;
  margin: 1em auto;
}
